html{
  min-height: 100%
}
body {
  margin: 0;
  font-family: sans-serif;
  padding: 0;
  background: url(/img/mario-bg.png) no-repeat;
  background-size: 100%;
  background-position: bottom;
  background-color: #95e8f3;
  min-height: 100%
}

form{
  padding: 20px;
  margin-top: 60px;
}

form button, form h5{
  margin: 20px 0;
}

input[type=text]:not(.browser-default):focus:not([randomly]),
input[type=email]:not(.browser-default):focus:not([randomly]),
input[type=password]:not(.browser-default):focus:not([randomly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: #ec407a;
  box-shadow: none;
}
